import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PettyCashTypeCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/cashBank/pettyCashType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/petty-cash-types"

class CreatePettyCashType extends Component {

  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCashType.error) {
      notification("error", this.props.pettyCashType.error);
      this.props.clearError();
    }

    if (this.props.pettyCashType.success) {
      this.props.history.replace(redirectTo);
    }
  }
  // Get All data diawal
  componentDidMount() {
    const { createPettyCashType } = this.props;
    createPettyCashType();
  }

  handleSubmit = value => {
    const { storePettyCashType } = this.props;

    // Uppercasing pettyCashType code
    // value.petty_cash_type_code = value.petty_cash_type_code.toUpperCase();
    // console.log(value);
    storePettyCashType(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="pettyCashTypes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <PettyCashTypeCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            pettyCashType={this.props.pettyCashType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, pettyCashType: state.pettyCashType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreatePettyCashType);
