import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Col, Checkbox} from "antd";

const { Option } = Select;
//const { TextArea } = Input;
const InputGroup = Input.Group;

class PettyCashTypeCreateForm extends Component {
  state = {
    check_code:false
  }
  
  handleChangeCode = () => {
    const {setFieldsValue} = this.props.form
    this.setState({check_code:!this.state.check_code})
    if(this.state.check_code===false){
      setFieldsValue({
        ["petty_cash_type_code"]: null,
      })
    }
  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { pettyCashType } = this.props;
    const { loading } = pettyCashType;
    const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = { 
      display: "flex", 
      width: "50%" 
    }

    const radioButtonStyle = { 
      flex: "1", 
      textAlign: "center" 
    }

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="pettyCashTypes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup compact>
              <Col span={20}>  
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="pettyCashTypes.add.code" />}
                  validateStatus={
                    error.petty_cash_type_code || getFieldError("petty_cash_type_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.petty_cash_type_code
                      ? error.petty_cash_type_code
                      : getFieldError("petty_cash_type_code")
                  }
                >
                  {getFieldDecorator("petty_cash_type_code", {
                    //initialValue: data.petty_cash_type_code,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(
                    <Input
                      disabled={this.state.check_code ? true : false}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{  
                      paddingTop:"5px",
                      paddingLeft: "8px",
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                  
                </Form.Item>
              </Col>
            </InputGroup>
            

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="name" />}
            >
              {getFieldDecorator("description", {
                //initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="accounts.pageHeader" />}
            >
              {getFieldDecorator("id_account", {
                //initialValue: data.id_account,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={pettyCashType.loadingFilterAccount}
                  showSearch
                  optionFilterProp="children"
                  //disabled={used ? true : false}
                >
                  {this.props.pettyCashType.accounts.map((data, key) => (
                    <Option key={key} value={data.id_account}>
                      {data.account_code + " " + data.account_name}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="transactionType" />}
            >
              {getFieldDecorator("transaction_type", {
               // initialValue: data.transaction_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group 
                  buttonStyle="solid" 
                  style={radioGroupStyle}
                >
                  <Radio.Button style={radioButtonStyle} value={1}><IntlMessages id="deposit" /></Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={2}><IntlMessages id="withdrawal" /></Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedPettyCashTypeCreateForm = Form.create({ name: "create_petty_cash_type" })(PettyCashTypeCreateForm);

export default WrappedPettyCashTypeCreateForm;
